import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Subject } from "rxjs";
import { Student } from "../models/student.model";



@Injectable({
  providedIn: "root"
})
export class StudentsService {
  constructor(private http: HttpClient) {}

  public studentsChanged = new Subject<any>();

  getStudents() {
    return this.http.get("http://localhost:8000/api/students");
  }

  getStudent(index: number) {
    return this.http.get("http://localhost:8000/api/students/" + index);
  }

  addStudent(student) {
    return this.http.post("http://localhost:8000/api/students/", student).toPromise()
      .then(
        (res) => {
          this.studentsChanged.next();
          return res;
        }
      )
      .catch(
        err => console.log(err)
      );
  }

  deleteStudent(index: number) {
    
    return this.http.delete("http://localhost:8000/api/students/" + index).toPromise()
      .then(
        (res)=>{
          this.studentsChanged.next();
          return res;
        }
    )
      .catch(
        err=>console.log(err)
      );
  }

  updateStudent(index: number, newStudent) {
    return this.http.post("http://localhost:8000/api/students/" + index, newStudent).toPromise()
      .then(
        (res) => {
          this.studentsChanged.next();
          return res;
        }
      )
      .catch(
        err => console.log(err)
      );
  }
}