import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';

@Injectable({
  providedIn: "root"
})
export class CoursesService {
  constructor(private http: HttpClient) {}

  public coursesChanged = new Subject<any>();

  getCourses() {
    return this.http.get("http://localhost:8000/api/courses");
  }

  getCourse(index: number) {
    return this.http.get("http://localhost:8000/api/courses/" + index);
  }

  addCourse(course) {
    return this.http
      .post("http://localhost:8000/api/courses/", course)
      .toPromise()
      .then(res => {
        this.coursesChanged.next();
        return res;
      })
      .catch(err => console.log(err));
  }

  deleteCourse(index: number) {
    return this.http
      .delete("http://localhost:8000/api/courses/" + index)
      .toPromise()
      .then(res => {
        this.coursesChanged.next();
        return res;
      })
      .catch(err => console.log(err));
  }

  updateCourse(index: number, newCourse) {
    return this.http
      .post("http://localhost:8000/api/courses/" + index, newCourse)
      .toPromise()
      .then(res => {
        this.coursesChanged.next();
        return res;
      })
      .catch(err => console.log(err));
  }
}
